import React from 'react'
import { Button, Table } from 'reactstrap'
import styled from 'styled-components'
import { Phone } from 'types'

interface SearchResultProps {
  entries: Phone[]
  onEditRequest: (phone: Phone) => void
  onDeleteRequest: (id: number) => void
}

const SearchResult: React.FC<SearchResultProps> = ({
  entries,
  onEditRequest,
  onDeleteRequest,
}) => (
  <>
    <h3>Resultados: {entries.length}</h3>
    <span className="text-secondary">
      * Se muestra un mximo de 100 nmeros por bsqueda.
    </span>
    <Table striped responsive>
      <Thead>
        <tr>
          <th>ID</th>
          <th>Nmero</th>
          <th>Info</th>
          <th>ltima vez llamado</th>
          <th>ltima vez que atendi</th>
          <th>Comentarios</th>
          <th>Das pospuesto</th>
          <th>Caractersticas</th>
          <th>Acciones</th>
        </tr>
      </Thead>
      <Tbody>
        {entries.map((phone) => (
          <tr key={phone.id}>
            <td>{phone.id}</td>
            <PhoneCell>{phone.phone}</PhoneCell>
            <td>{phone.info}</td>
            <td>{phone.unansweredDate || phone.fulfilledOn || 'Nunca'}</td>
            <td>{phone.answeredOn || 'Nunca'}</td>
            <td>{phone.comments}</td>
            <td>{phone.postponedDays}</td>
            <td>
              {[
                phone.noCall && 'No visitar',
                phone.nonExistent && 'No existe',
                phone.noWeekends && 'No llamar los finde',
              ]
                .filter(Boolean)
                .join(', ')}
            </td>
            <ActionCell>
              <Button color="warning" onClick={() => onEditRequest(phone)}>
                Editar
              </Button>
              <Button color="danger" onClick={() => onDeleteRequest(phone.id)}>
                Borrar
              </Button>
            </ActionCell>
          </tr>
        ))}
      </Tbody>
    </Table>
  </>
)

const Thead = styled.thead`
  text-align: center;

  > tr > th {
    vertical-align: middle;
  }
`

const Tbody = styled.tbody`
  text-align: center;
`

const PhoneCell = styled.td`
  white-space: nowrap;
`

const ActionCell = styled.td`
  white-space: nowrap;

  button:not(:first-of-type) {
    margin-left: 1em;
  }
`

export { SearchResult }
